.fileModule {
    display: flex;
    flex-direction: column;
    padding: 20px;
    align-items: center;
}
.uploadFile {
    width: 1050px;
    border: 1px solid grey;
    padding-bottom: 15px;
}


.wrap {
    width: 1200px;
